.advantages {
  padding-top: 0;
  padding-bottom: 0;
  background-color: $section-bg-primary;
}

.advantages__list {
  text-align: left;

  @media screen and (max-width: 767px) {
    margin-bottom: -38px;
  }

  @media screen and (min-width: $tablet) {
    display: flex;
    margin-left: -30px;
    margin-right: -30px;
    padding-right: 38px;
  }
  @media screen and (min-width: $desktop) {
    margin-left: -15px;
    margin-right: -15px;
    padding-right: 0;
  }
}

.advantages__item {
  text-align: left;

  @media screen and (max-width: 767px) {
    max-width: 230px;
    margin-bottom: 38px;
  }

  @media screen and (min-width: $tablet) {
    flex-basis: calc((100% - 3 * 30px) / 3);
    margin-left: 30px;
    margin-right: 30px;
  }
  @media screen and (min-width: $desktop) {
    flex-basis: calc((100% - 3 * 15px) / 3);
    margin-left: 15px;
    margin-right: 15px;
  }
}

.advantages__item {
  &:first-child::before {
    content: url('../images/icons/icon-milk.svg');
  }
  &:nth-child(2)::before {
    content: url('../images/icons/icon-fruit.svg');
  }
  &:last-child::before {
    content: url('../images/icons/icon-caramel.svg');
  }
}

.advantages__quantity {
  font-family: $secondary-font-family;
  font-size: 36px;
  font-weight: 400;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $primary-color;
  margin-top: 16px;
  margin-bottom: 16px;

  @media screen and (min-width: $tablet) {
    font-size: 48px;
    margin-top: 36px;
    margin-bottom: 13px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 58px;
    margin-top: 40px;
    margin-bottom: 15px;
  }
}

.advantages__description {
  font-family: $primary-font-family;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.66;
  letter-spacing: 0.02em;
  color: $font-color;

  @media screen and (min-width: $tablet) {
    font-size: 14px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 16px;
  }
}
